@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body{
    position: relative;
    
}

body{
  margin: 0px;
  padding: 0%;
  height: 100%;
  
}
.App {
  text-align: center;
  position: relative;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

*{
    box-sizing: border-box;
}
/*-----------------------------------------  Web links -----------------------------------------    */
.navbar {
    z-index: 10;
    background-color: rgb(255, 255, 255);
    width: 100%;
    position: fixed;
    display: flex;
    justify-content: space-between;
    text-align: center;
    
}
.navbar-logo{
    padding-left: 1rem;
    background-color: rgb(255, 255, 255);
    width: 80%;
}

.navbar-logo:hover{
    background-color: #fff176;
}
.navbar-items ul{ 
    margin: 0%;
    padding: 0%;
   display: flex;
    
}
.navbar-items li {
    list-style: none;
}
.navbar-items li a{
    font-size: 1.5em;
    text-decoration: none;
    color: rgb(33, 36, 36);
    padding: 2rem;
    display: block;
}
.navbar-items li a:hover{
background-color: #4482DF;
color: cornsilk ;
}

.navbar-mobile-menu {
    display: none;
    
  }
/* --------------------------  Mobile links  ----------------------------------------- */


.navbar-items-mobile{
    display: none;
}
.navbar-mobile-menu {
    display: none;
  }
  .navbar-items-mobile-active{
    display: none;
}
  .navbar-mobile-menu-active {
    display: none;
  }
  
  .bar1, .bar2, .bar3 {
    width: 1.1rem;
    height: .15rem;
    background-color: #333;
    margin: .25rem 0;
    transition: 0.4s;
  }
/* Responsive sites */
@media screen and (max-width: 1000px){
    .navbar-logo{
        padding-left: 1rem;
        background-color: rgb(255, 255, 255);
        width: 60%;
    } 
    .navbar-items li a{
       font-size: 1em;
       padding: 1rem;
    }
}
/* This is the break point to switch to hamberger menu */
@media screen and (max-width: 545px){  
    .navbar-mobile {
        z-index: 10;
        height: 8%;
        width: 100%;
        position: fixed;
        display: flex;
        background-color: rgb(255, 255, 255);
        
        
    }
    .navbar-logo-cotainer{
     overflow: visible;
      position:relative;
     
    }
    .navbar-logo{
        position: absolute;
        left: 0;
        padding: .1rem;
        background-color: transparent;
        margin-left: 2em;
        max-width: 100%;
    height: auto;
    width: auto\9;
       width: 20%;
       
    }
    
    .navbar-mobile-menu {
        display: inline-block;
        position: absolute;
        right:  0;
        padding: 1rem;
        cursor: pointer;
      }
      .navbar-mobile-menu-active {
        display: inline-block;
        position: absolute;
        right:  0;
        top: 0;
        padding: 1rem;
        cursor: pointer;
      }
      .navbar-mobile-menu-active .bar1 {
        transform: rotate(-45deg) translate(-1px, 5px) ;
      }
      .navbar-mobile-menu-active .bar2 {
        opacity: 0;
      }
      .navbar-mobile-menu-active .bar3 {
        transform: rotate(45deg) translate(-5px, -8px) ;
      }
    .navbar-items {
       display: none;
     }
     ul{
        padding-left: 0%;
        background-color: white;
     }
     .navbar-items-mobile-active{
        margin-top: 2%;
        background-color: white;
       
        width: 100%;
       display: flex;
       flex-direction: column;
       justify-content: space-between;
    }
    .navbar-items-mobile-active li {
        list-style: none;
    }
    .navbar-items-mobile-active li a{
        font-size: 0.8em;
        text-decoration: none;
        color: rgb(33, 36, 36);
        padding: .8rem;
        display: block;
    }
    .navbar-items-mobile-active li a:hover{
      font-size: 1rem;
      color: #4482DF;
    
    }
     
}
.about-container{
    position: relative;
    
   padding-top: 10%;
    color: black;
    text-align: justify;
    text-justify: inter-word;
}

footer{
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: repeat(2, auto);
     justify-content: center;
    color: rgb(0, 0, 0);
    width: 100%;
    left: 0px;
    bottom: 0px;
    background-color: rgb(219, 223, 243);
    padding: 2%;
    font-size: 1rem;
}
ul{
    list-style: none;
}
.social-media-icons{
    width: 2.5rem;
    height: 2.5rem;
    margin: 1rem;
}
.contact-icons{
    width: 2.5rem;
    height: 2.5rem;
    
}
.location-column{
    display: flex;
    flex-direction: row;
    padding: 2rem;
}

.location-map{
    width: 10rem;
    height: 10rem;
}
.location-address{
    text-align: justify;
    text-justify: inter-word;
}
.contact-column{
    padding: 2rem;
    display: flex;
    flex-direction: column;
   

}
@media screen and (max-width: 1000px){
    footer{
        display: grid;
        grid-template-columns: repeat(2, auto);
       
    }
    .logo{
        width: 10rem;
        height: 10rem;
    }
    .location-map{
        width: 8rem;
        height: 8rem;
    }
}


@media screen and (max-width: 600px){
    footer{
        display: grid;
        grid-template-columns: repeat(1, auto);
        font-size: 0.7rem;
        padding: 1rem;
       
    }
    .location-column{
        width: 100%;
        display: flex;
        flex-direction: row;
        
    }
    
    .location-address{
        width: 100%;
        text-align: justify;
        text-justify: inter-word;
    }
    .social-media-icons{
        width: 1.5rem;
        height: 1.5rem;
        margin: 1rem;
    }
    .contact-icons{
        width: 1.5rem;
        height: 1.5rem;
        
    }
  
}
*{
    font-family: 'PT Sans', sans-serif;
 }  
.contact-dialogue{
     color: black
 }
 .contact{
    padding-top: 5%;
    display: flex;
    flex-direction: row;
    width: 100%;
} 
.contact-image{
    background-image:  url(/static/media/contact_us.4b2d3700.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    width: 90%;
}
.contact-text{
    position: absolute;
    left: 0;
    color: rgb(139, 139, 139);
    padding: 3rem;
    text-align: justify;
    text-justify: inter-word;
}

.contact-form{
    color: white;
    background-color: rgb(81, 136, 219);
    padding: 5rem;
    border-radius: 0.12rem 3rem 2rem 0.1rem;
    width: 30%;
}
input{
    padding: .7rem;
    border-radius: 1rem;
    box-shadow: 5px 5px #1f1f1f;
}
.comment{
    resize: vertical;
    border-radius: 0.75rem;
    box-shadow: 5px 5px #1f1f1f;
}
.contact-text-mobile{
    display: none;
}
@media screen and (max-width: 1000px){
    .contact{
        padding-top: 10%;
        display: flex;
        flex-direction:column;
        width: 100%;
    }  
    .contact-dialogue{
        color: black
    }
    .contact-image{
       display: none
    }
    .contact-text{
       display: none;
    }
    .contact-text-mobile{
        display: block;
        color: rgb(139, 139, 139);
        font-size: .75rem;
    }
    .contact-image-mobile{
        width: 100%;
        height: 30%;
    }
    .contact-form{
        color: white;
        background-color: rgb(81, 136, 219);
        padding: 2rem;
        border-radius: 3% 3% 0% 0%;
        width: 100%;
    }
    input{
       width: 80%;
    }
    .comment{
        width: 80%;
    }
}

@media screen and (max-width: 600px){
    .contact{
        padding-top: 15%;
        display: flex;
        flex-direction:column;
        width: 100%;
    }  
    .contact-dialogue{
        color: black
    }
    .contact-text-mobile{
        display: block;
        color: rgb(139, 139, 139);
        font-size: .5rem;
    }
}
.hero-image-container{
width: 100%;
height: 100%;
position: relative;
}
.hero-image  {
    /* Use "linear-gradient" to add a darken background effect to the image (photographer.jpg). This will make the text easier to read */
    
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/SolarPanelGrid.61c280c1.jpg);
    
    /* Set a specific height */
    height: 100%;
    padding-top: 15rem;
    padding-bottom: 10rem ;
    /* Position and center the image to scale nicely on all screens */
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    animation: changeimage 4s ease 4s infinite alternate;
   
  }
  @keyframes changeimage {
    from {background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/SolarPanelGrid.61c280c1.jpg); ;}
    to {background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/static/media/volt_meter.88371aa9.jpg);background-size: 100%;}
  }

